.banner{
  background-position: center;
  background-size: cover;
  width:100%;
  min-width: 100vh;
  z-index: 1;
  position: relative;
	top: 0;
	left: 0;
}

#logo {
    overflow: hidden;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction:row;
        flex-direction:row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
#myImg {
    position: absolute;
    z-index: 2;
    margin: auto;
    max-width: 70%;
    min-width: 70%;
  }

.responsive {
    max-width: 100%;
    height: auto;
}



