#portifolio{
  padding-top: 50px;
  background-color: #EFEFEF;
}
#portifolio-titulo {
  padding-bottom: 15px;
}

.empresas{
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  height: 60px;
  width: 100px;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  padding-left: 30px;
  margin-bottom: 15px;
}
.empresas:hover {
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
}

#carousel{
  padding-top: 0px;
}



