ul > li {
  list-style-type: none;
}

.container{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: none;
  position:fixed;
  z-index: 20;
  width: 100%;
  -webkit-transition: 0.35s all ease;
  -o-transition: 0.35s all ease;
  transition: 0.35s all ease;
  font-family: 'Work Sans', sans-serif;
  height: 100px;
  top:20px;
}

.menu{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 1000px;
}

#icon{
  position: absolute;
  top: 20px;
  right: 30px;
}

.scrolled {
  height: 75px;
  background-color: white;
  -webkit-transition: 0.35s all ease;
  -o-transition: 0.35s all ease;
  transition: 0.35s all ease;
  -webkit-box-shadow: 2px 2px 5px grey;
          box-shadow: 2px 2px 5px grey;
}
a:link{
  text-decoration: none;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
a:hover{
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  cursor: pointer;
  color:black;
}
a:visited{
  color:blue;
}

.button{
  color:red;
  font-size: 1.2em;
  text-decoration: none;
}



